import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { Ingredient } from '../../shared/ingredient.model';
import { ShoppingService } from '../shopping-list.service';
import { NgForm } from '@angular/forms';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-shopping-edit',
  templateUrl: './shopping-edit.component.html',
  styleUrls: ['./shopping-edit.component.css']
})
export class ShoppingEditComponent implements OnInit , OnDestroy {
  @ViewChild('f') slForm : NgForm; 
  sub: Subscription;
  editMode = false;
  editedItemIndex :number;
  editedItem : Ingredient;

  constructor(private shopService:ShoppingService) { }

  ngOnInit() {
   this.sub = this.shopService.startedEditing
    .subscribe(
      (index: number) => {
        this.editedItemIndex = index;
        this.editMode = true;
        this.editedItem = this.shopService.getIngredient(index);
        console.log( "edit ",this.editedItem.name)
        this.slForm.setValue({
          name: this.editedItem.name,
          amount: this.editedItem.amount
        })
      }

    );
  }

  OnAddItem(form:NgForm){
    const value = form.value;
    const newIngredient = new Ingredient(value.name,value.amount)
    if (this.editMode) {
      this.shopService.updateIngredient(this.editedItemIndex,newIngredient)
    }
    else 
    {
      this.shopService.addIngredients(newIngredient);
    }
    this.editMode = false;
    form.reset()
  
  }

  onClear(){
    this.editMode = false;
    this.slForm.reset();
  }

  onDelete(){
    this.shopService.deleteIngredients(this.editedItemIndex);
    this.onClear();


  }
  
  ngOnDestroy(){
    this.sub.unsubscribe();
  }

}



